/** Media Queries **/
.navbar-brand span {
  font-family: "Indie Flower", cursive;
  font-size: 3rem;
}

.navbar-brand {
  margin-right: 0;
  padding: 0;
}
@media (min-width: 1024px) {
  .navbar-brand .navbar-brand {
    margin-right: 0;
    padding: 0 1em;
  }
}

.navbar-nav {
  align-items: center;
}

.navbar .navbar-nav .nav-link {
  color: #FFFFFF;
  font-size: 1.5em;
  padding: 0.5em 1em;
}

.navbar .navbar-nav .nav-link:hover {
  color: #FB9A09;
}

@media screen and (min-width: 992px) {
  .navbar-brand img {
    width: 70px;
  }
}
.navbar-toggler-icon {
  background-image: url("data:image/svg+xml,%3csvg xmlns= 'http://www.w3.org/2000/svg' viewBox= '0 0 30 30' %3e%3cpath stroke= 'white' stroke-linecap= 'round' stroke-miterlimit= '10' stroke-width= '3' d= 'M4 7h22M4 15h22M4 23h22' /%3e%3c/svg%3e");
}

.navbar-toggler {
  border: 2px solid white;
}

.navbar-toggler:focus {
  box-shadow: none;
}

.bg-navbar {
  background-color: #1B8B9F;
}